import domtoimage from 'dom-to-image';
import { saveAs } from 'file-saver';
import React, { useState, useEffect } from 'react';
import { Helmet } from "react-helmet";
import '../assets/stylesheet/all.scss';

import downloadImg from '../assets/javascript/html2canvasImg';
import moment from 'moment';

//img url
import rainbowUrl from '../assets/images/happyRetiredLife/rainbow.png'; //
import bgWallUrl from '../assets/images/happyRetiredLife/bg-wall.png'; //
import logoUrl from '../assets/images/happyRetiredLife/logo.png';
import decorationImg1 from '../assets/images/happyRetiredLife/img1.svg';//
import decorationImg2 from '../assets/images/happyRetiredLife/img2.svg';//
// import './index.css'
// <i>

/*
-- :( ios15.2)
    * 
    * ???
        *  getImg setTimeout 
*/
function filter(node) {
    return (node.tagName !== 'i');
}
const DomToImg = () => {
    const bgWall = {//
        backgroundImage: `url(${bgWallUrl})`,
        backgroundColor: '#FFF'
    }

    let [showText, setShowText] = useState(false)

    let [date, setDate] = useState(moment().format('YYYY-MM-DD')); //input 
    downloadImg();
    const handleDate = (e) => { //input 
        setDate(e.target.value)
    }

    const bgWapperStyle = {
        /* ios15  */
        fontFamily: "Helvetica, Tahoma, Arial, 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', STXihei, 'Microsoft YaHei',SimHei",
        backgroundImage: `url(${rainbowUrl})` //
    }
    // png
    let [hide, setHide] = useState(false)
    const getImg = () => {
        alert('click ')
        setShowText(true)
        const node = document.getElementById("downloadJPG");
        let viewportWidth = document.getElementById("viewport");
        viewportWidth.setAttribute('content', 'width=795px');
        setTimeout(() => {
            domtoimage.toJpeg(node).then((defaultUrl) => {
                setHide(true)
                const img = new Image();
                // img.dpi = 96 * 3
                img.src = defaultUrl;
                img.setAttribute('className', 'pngImg');// 
                // png
                document.getElementById('export-img').appendChild(img);
                //  saveAs(defaultUrl, '.png')
                img.addEventListener('click', () => {
                    var link = document.createElement('a');
                    link.download = '.png';
                    link.href = defaultUrl;
                    link.click();
                })
            }).catch(() => {
                console.log("error")
            })
        }, 2000)
    }
    // pngdom
    const getBlobPng = () => {
        const node = document.getElementById("node");
        domtoimage.toBlob(node).then((blob) => {
            // file-save 
            saveAs(blob, '.png')
        })
    }
    return (
        <>
            <Helmet>
                <meta name="viewport" content="width=device-width, initial-scale=1.0, shrink-to-fit=no" id="viewport" />
            </Helmet>
            <div className='dom-to-img'>
                <div className={`${hide ? 'd-none' : ''}`}>
                    <div id="node" className={`node-content`}>
                        <div className="bg-wapper bg-life-light-pink lifebg-fixed-bottom p-3 bg-light" id="downloadJPG">
                            <div className="container">
                                <div className="row">
                                    <h1 className="text-center fw-bolder title"><span className="text-life-dark-pink"></span><span className="text-life-blue"></span></h1>
                                    <div className="bg-wall pt-4 pb-2 position-relative" style={bgWall}>
                                        <div className="decoration-icon">
                                            <img src={decorationImg1} alt="-" />
                                            <img src={decorationImg2} alt="-" />
                                        </div>
                                        {/*  */}
                                        <div className="col-12 position-relative">
                                            <div className="life-line">
                                                <div className="row justify-content-between g-0">
                                                    <div className="col-2 position-relative">
                                                        <p className="fw-bolder mb-3 text-center dot-box-first life-title"></p>
                                                        <div className="d-flex flex-column flex-md-row align-items-center justify-content-center text-center mt-5">
                                                            <span className="d-block"><input className="form-control w-50 d-inline-block" /></span>
                                                            <p className=""></p>
                                                        </div>
                                                    </div>
                                                    <div className="col-2"><p className="text-life-dark-pink fw-bolder mb-3 text-center life-title"></p></div>
                                                    <div className="col-2 position-relative">
                                                        <p className="fw-bolder mb-3 text-center dot-box life-title"></p>
                                                        <div className="d-flex flex-column flex-md-row align-items-center justify-content-center text-center mt-5">
                                                            <span className="d-block"><input className="form-control w-50 d-inline-block" /></span>
                                                            <p className=""></p>
                                                        </div>
                                                    </div>
                                                    <div className="col-2"><p className="text-life-dark-pink fw-bolder mb-3 text-center life-title"></p></div>
                                                    <div className="col-2 position-relative">
                                                        <p className="fw-bolder mb-3 text-center dot-box life-title"></p>
                                                        <div className="d-flex flex-column flex-md-row align-items-center justify-content-center text-center mt-5">
                                                            <span className="d-block"><input className="form-control w-50 d-inline-block" /></span>
                                                            <p className=""></p>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        {/* input checkbox */}
                                        <div className="col-12 my-3">
                                            <h3>1. </h3>
                                            <div className="first-option d-flex flex-wrap">
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" id="bankSavings" />
                                                    <label className="form-check-label" htmlFor="bankSavings"></label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" id="insurerSavings" />
                                                    <label className="form-check-label" htmlFor="insurerSavings"></label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" id="stock" />
                                                    <label className="form-check-label" htmlFor="stock"></label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" id="fund" />
                                                    <label className="form-check-label" htmlFor="fund"></label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" id="propertyAsset" />
                                                    <label className="form-check-label" htmlFor="propertyAsset"></label>
                                                </div>
                                                <div className="form-check m-2 d-flex justify-content-around">
                                                    <input className="form-check-input" type="checkbox" id="firstOther" />
                                                    <label className="form-check-label" htmlFor="firstOther"></label>
                                                    <input className={`form-control w-80`} type="text" id="firstOther" />
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-12 my-3">
                                            <h3>2. </h3>
                                            <div className="secondary-option d-flex flex-wrap">
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" name="secondaryOption" id="travelWorld" />
                                                    <label className="form-check-label" htmlFor="travelWorld"></label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" name="secondaryOption" id="learn" />
                                                    <label className="form-check-label" htmlFor="learn"></label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" name="secondaryOption" id="charity" />
                                                    <label className="form-check-label" htmlFor="charity"></label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" name="secondaryOption" id="secondaryBusiness" />
                                                    <label className="form-check-label" htmlFor="secondaryBusiness"></label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" name="secondaryOption" id="takeCareKid" />
                                                    <label className="form-check-label" htmlFor="takeCareKid"></label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" name="secondaryOption" id="helpForChildren" />
                                                    <label className="form-check-label" htmlFor="helpForChildren"></label>
                                                </div>
                                                <div className="form-check m-2 d-flex justify-content-around">
                                                    <input className="form-check-input" type="checkbox" name="secondaryOption" id="Secondaryother" />
                                                    <label className="form-check-label" htmlFor="Secondaryother"></label>
                                                    <input className={`form-control w-80`} type="text" />
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-12 my-3">
                                            <h3>3. </h3>
                                            <div className="third-option d-flex flex-wrap">
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" name="thirdOption" id="thirdOne" />
                                                    <label className="form-check-label" htmlFor="thirdOne"></label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" name="thirdOption" id="thirdTwo" />
                                                    <label className="form-check-label" htmlFor="thirdTwo"></label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" name="thirdOption" id="thirdThree" />
                                                    <label className="form-check-label" htmlFor="thirdThree"></label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" name="thirdOption" id="thirdFour" />
                                                    <label className="form-check-label" htmlFor="thirdFour"></label>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-12 my-3">
                                            <h3>4. </h3>
                                            <div className="four-option d-flex flex-wrap">
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="radio" name="fourOption" id="500" />
                                                    <label className="form-check-label" htmlFor="500">500 </label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="radio" name="fourOption" id="1000" />
                                                    <label className="form-check-label" htmlFor="1000">1,000 </label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="radio" name="fourOption" id="1200" />
                                                    <label className="form-check-label" htmlFor="1200">1,200 </label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="radio" name="fourOption" id="2000" />
                                                    <label className="form-check-label" htmlFor="2000">2,000 </label>
                                                </div>
                                                <div className="form-check m-2 d-flex justify-content-around">
                                                    <input className="form-check-input" type="radio" name="fourOption" id="fourOther" />
                                                    <label className="form-check-label" htmlFor="fourOther"></label>
                                                    <input className={`form-control w-80 `} type="text" id="fourOther" />
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-12 my-3">
                                            <h3>5. </h3>
                                            <div className="five-option d-flex flex-wrap">
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="radio" name="fiveOption" id="2" />
                                                    <label className="form-check-label" htmlFor="2">2 </label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="radio" name="fiveOption" id="3" />
                                                    <label className="form-check-label" htmlFor="3">3 </label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="radio" name="fiveOption" id="4" />
                                                    <label className="form-check-label" htmlFor="4">4 </label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="radio" name="fiveOption" id="5" />
                                                    <label className="form-check-label" htmlFor="5">5 </label>
                                                </div>
                                                <div className="form-check m-2 d-flex justify-content-around">
                                                    <input className="form-check-input" type="radio" name="fiveOption" id="fiveOther" />
                                                    <label className="form-check-label" htmlFor="fiveOther"></label>
                                                    <input className={`form-control w-80 `} type="text" id="fiveOther" />
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-12 my-3">
                                            <h3>6. </h3>
                                            <div className="six-option d-flex flex-wrap">
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" name="sixOption" id="sixOne" />
                                                    <label className="form-check-label" htmlFor="sixOne"></label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" name="sixOption" id="sixTwo" />
                                                    <label className="form-check-label" htmlFor="sixTwo"></label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" name="sixOption" id="sixThree" />
                                                    <label className="form-check-label" htmlFor="sixThree"></label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" name="sixOption" id="sixFour" />
                                                    <label className="form-check-label" htmlFor="sixFour"></label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="checkbox" name="sixOption" id="sixSix" />
                                                    <label className="form-check-label" htmlFor="sixSix"></label>
                                                </div>
                                                <div className="form-check m-2 d-flex justify-content-around">
                                                    <input className="form-check-input" type="checkbox" name="sixOption" id="sixOther" />
                                                    <label className="form-check-label" htmlFor="sixOther"></label>
                                                    <input className={`form-control w-80 `} type="text" id="sixOther" />
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-12 my-3">
                                            <h3>7. ?</h3>
                                            <div className="steven-option d-flex flex-wrap">
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="radio" name="stevenOption" id="stevenOne" />
                                                    <label className="form-check-label" htmlFor="stevenOne">5,000 </label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="radio" name="stevenOption" id="stevenTwo" />
                                                    <label className="form-check-label" htmlFor="stevenTwo">10,000 </label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="radio" name="stevenOption" id="stevenThree" />
                                                    <label className="form-check-label" htmlFor="stevenThree">20,000 </label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="radio" name="stevenOption" id="stevenFour" />
                                                    <label className="form-check-label" htmlFor="stevenFour">30,000 </label>
                                                </div>
                                                <div className="form-check m-2">
                                                    <input className="form-check-input" type="radio" name="stevenOption" id="stevenFive" />
                                                    <label className="form-check-label" htmlFor="stevenFive">50,000 </label>
                                                </div>
                                            </div>
                                        </div>
                                        {/* download button */}
                                        <div className="col-12 mb-2 text-center row px-md-5">
                                            <div className="my-3 col-md-6">
                                                <label htmlFor="name" className="text-life-blue fw-bolder"></label>
                                                <input className="form-control d-inline-block name" />
                                            </div>
                                            <div className="my-3 col-md-6">
                                                <label htmlFor="date" className="text-life-blue fw-bolder"></label>
                                                <input type="date" className="form-control d-inline-block date" value={date} onChange={handleDate} />
                                            </div>
                                            <p> 7</p>
                                        </div>
                                    </div>
                                    {/* logo */}
                                    <footer className="text-center position-relative mt-2">
                                        <img src={logoUrl} className="img-fluid " alt="golden-logo" width="150" />
                                        <small className="text-light golden-num">()2021090001</small>
                                    </footer>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="my-actions">
                        <button onClick={getImg} className='action'>png</button>
                        <button onClick={getBlobPng} className='action'>png</button>
                        {/* <button onClick={getJpeg} className='action'>jpeg</button>
                        <button onClick={getSVG} className='action'>svg</button> */}
                    </div>
                </div>
                <div id="export-img" className="my-image">
                </div>
                <p className={`text-center fw-bolder py-3 ${hide ? 'd-block' : 'd-none'}`}></p>
            </div>
        </>)
}

export default DomToImg;
